<template>
    <div class="docs-container">
        <div class="docs-info">
            <div class="docs-info-title">
                WhapiChat API Documentation
            </div>
            <div class="docs-info-content">
                Info content
            </div>
        </div>
        <div class="api-container">
            <div class="api-about-title">
                METHODS
            </div>
            <div class="methods">
                <div class="api-column" v-for="api in apis" :key="api.url">
                    <div :class="api.type === 'POST' ? 'api-box green-lighten' : 'api-box blue-lighten'"
                        @click="api.boxIsOpened = !api.boxIsOpened">
                        <div class="api-type" :class="api.type === 'POST' ? 'green' : 'blue' ">
                            {{api.type}}
                        </div>
                        <div class="api-url">
                            {{api.url}}
                        </div>
                        <div v-if="!api.boxIsOpened" @click.self="api.boxIsOpened = !api.boxIsOpened"
                            class="open-box-icon">
                            <img src="@/assets/icons/chevron-down.png" alt="">
                        </div>
                        <div v-else @click.self="api.boxIsOpened = !api.boxIsOpened" class="open-box-icon">
                            <img src="@/assets/icons/chevron-up.png" alt="">
                        </div>
                    </div>
                    <v-expand-transition>
                        <div :class="api.type === 'POST' ? 'api-info green-lighten border-top-green' : 'api-info blue-lighten border-top-blue'"
                            v-if="api.boxIsOpened">
                            asdas
                        </div>
                    </v-expand-transition>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                apis: [{
                        type: "POST",
                        url: '/send_message/:token',
                        boxIsOpened: false,
                        data: {
                            number: '<String>',
                            message: '<String>'
                        }
                    },
                    {
                        type: "GET",
                        url: '/get_message_history/:token/:number/:limit',
                        boxIsOpened: false,
                    },
                    {
                        type: "GET",
                        url: '/get_chats/:token',
                        boxIsOpened: false,
                    },
                    {
                        type: "GET",
                        url: '/get_profile_picture/:token',
                        boxIsOpened: false,
                    },
                    {
                        type: "GET",
                        url: '/get_info/:token',
                        boxIsOpened: false,
                    },
                    {
                        type: "POST",
                        url: '/send_image/:token',
                        boxIsOpened: false,
                        data: {
                            b64: '<Base64>',
                            b64Name: '<File Name>',
                            number: '<String>',
                            type: '<Image Type>'
                        }
                    }
                ]
            }
        }
    }
</script>

<style lang="scss">
    @import "@/scss/mixins.scss";

    .blue {
        background-color: var(--whapichat-blue)
    }

    .blue-lighten {
        background-color: lighten($color: #305691, $amount: 58)
    }

    .green {
        background-color: rgb(2, 175, 2);
    }

    .green-lighten {
        background-color: lighten($color: rgb(2, 175, 2), $amount: 58)
    }

    .border-top-blue {
        border-top: 1px solid #b9cff5;
    }

    .border-top-green {
        border-top: 1px solid #bafcba;
    }

    .docs-container {
        @include d-flex(column, flex-start, stretch);
        width: 100%;
        height: 100%;
        flex: 1 0 1px;
        font-family: var(--whapichat-font);
        gap: 3%;

        .docs-info {
            @include d-flex(column, flex-start, stretch);
            gap: 1%;
            flex: 0 0 auto;
            width: 100%;
            background-color: var(--whapichat-blue);
            @include box-shadow;
            color: white;
            border-radius: 3px;
            padding: .5vh .5vw;

            .docs-info-title {
                font-weight: 500;
                @include d-flex(row, center, center);
                font-size: 1.8vmin;
            }

            .docs-info-content {
                font-size: 1.5vmin;
            }
        }

        .api-container {
            @include d-flex(column, flex-start, stretch);
            flex: 1 0 1px;
            width: 100%;
            overflow: auto;
            background-color: #fff;
            @include box-shadow;
            border-radius: 3px;
            gap: 1%;

            .api-about-title {
                font-weight: bold;
                flex: 0 0 auto;
                width: 100%;
                padding: 1vh .5vw;
                font-size: 1.5vmin;
            }

            .methods {
                @include d-flex(column, flex-start, stretch);
                flex: 1 0 1px;
                overflow: auto;
                width: 100%;
                gap: 2%;
                padding: .5vh .5vw;

                .api-column {
                    @include d-flex(column, flex-start, stretch);
                    width: 100%;
                    border-radius: 2px;
                    @include box-shadow;

                    .api-box {
                        @include d-flex(row, space-between, center);
                        width: 100%;
                        padding: .8%;
                        gap: 1%;

                        .api-type {
                            @include d-flex(column, center, center);
                            flex: 0 0 auto;
                            width: 6vmin;
                            height: 3vmin;
                            color: #fff;
                            border-radius: 3px;
                            font-size: 1.3vmin;
                        }

                        .api-url {
                            @include d-flex(row, flex-start, center);
                            flex: 1 0 1px;
                            height: 3vmin;
                            font-weight: 500;
                            font-size: 1.6vmin;
                        }

                        .open-box-icon {
                            @include d-flex(column, center, center);
                            flex: 0 0 auto;
                            height: 3vmin;
                            cursor: pointer;
                        }
                    }

                    .api-info {
                        padding: .8%;
                    }
                }



                &::-webkit-scrollbar {
                    width: .5vmin;
                    height: .5vmin;
                    border-radius: 10px;
                }

                &::-webkit-scrollbar-thumb {
                    background: #aca9a9;
                    border-radius: 10px;
                }
            }
        }
    }
</style>