import Vue from 'vue'
import Vuex from 'vuex'
import _ from "lodash"

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    showUserLoginForm:false,
    logs:[],
    devTerminal:"",
  },
  mutations: {
    setLogs(state,item){
      state.logs.push(item)
    },
    setDevTerminal(state,item){
      state.devTerminal = item
    },
    setShowUserLoginForm(state,item){
      state.showUserLoginForm = item
    },
    clearLogs(state){
      state.logs = [];
    },
    clearDevTerminal(state){
      state.devTerminal = ""
    }
  },
  getters:{
    getShowUserLoginForm(state){
      return state.showUserLoginForm
    },
    getLogs(state){
      return state.logs
    },
    getDevTerminal(state){
      return state.devTerminal
    }
  }
})
