<template>
    <div class="link-creator-card">
        <div class="input-areas">
            <div>
                <p v-if="phoneNumber.length > 0">International phone starting with country code like 901234567890*</p>
                <input v-model="phoneNumber" class="input" required
                    placeholder="International phone starting with country code like 901234567890*"
                    :maxlength="maxLengthPhoneNumber" type="text">
                <div class="input-counter">
                    {{phoneNumber.length}} / {{maxLengthPhoneNumber}}
                </div>
            </div>
            <div>
                <p v-if="message.length>0">Start message text*</p>
                <input v-model="message" class="input" required placeholder="Start message text*" type="text"
                    :maxlength="maxLenghtMessage">
                <div class="input-counter">
                    {{message.length}} / {{maxLenghtMessage}}
                </div>
            </div>
        </div>

        <v-slide-x-transition>
            <div class="url-area" v-if="message.length > 0 && phoneNumber.length > 10">
            <p v-if="sendMessageURL.length > 0">Your link</p>
            <input id="msgURL" readonly :value="sendMessageURL" class="input" required placeholder="Your link"
                type="text">
            <div class="info-text">Copy this link and put it on the site or in social networks. <br>
                When clicking on the link from the phone, the client will display WhatsApp with the message
                <b>"{{message}}"</b> for the number <b>{{phoneNumber}}</b>.</div>
            <div class="copy-content">
                <p class="before-copy-text" v-if="copyIcon">Copy
                </p>
                <p class="after-copy-text"  v-if="copyIconActive">Text copied to clipboard!</p>
                <v-icon class="copy-icon" @click="copy(), copyIcon=false, copyIconActive=true"
                    @mouseenter="copyIcon = true" @mouseleave="copyIcon = false, copyIconActive = false">
                    mdi-content-copy</v-icon>
            </div>
        </div>
        </v-slide-x-transition>
    </div>
</template>

<script>
    export default {

        data() {
            return {
                phoneNumber: "",
                maxLengthPhoneNumber: 13,
                message: "",
                maxLenghtMessage: 10000,
                copyIcon: false,
                copyIconActive: false
            }
        },
        computed: {
            sendMessageURL() {
                let str = encodeURIComponent(this.message);
                return `https://api.whatsapp.com/send?phone=${this.phoneNumber}&text=${str}`
            }
        },
        methods: {
            copy() {
                let msgArea = document.getElementById('msgURL');
                msgArea.focus();
                msgArea.select();
                try {
                    var successful = document.execCommand('copy');
                    var msg = successful ? 'successful' : 'unsuccessful';
                    //console.log('Copying text command was ' + msg);
                } catch (err) {
                   // console.log('Oops, unable to copy');
                }
            }
        }

    }
</script>

<style lang="scss">

@import "@/scss/mixins.scss";

    .link-creator-card {
        @include d-flex(row,flex-start,stretch);
        flex-wrap:wrap;
        flex: 1 0 1px;
        width: 100%;
        gap: 1%;
        font-family: var(--whapichat-font);

        .input-areas {
            @include d-flex(column,flex-start,stretch);
            padding: 1%;
            flex: 1 0 1px;
            gap: 20px;
            background-color: #ebebeb;
            border-radius: 10px;
            @include box-shadow;
    
            div {

                p {
                    @include d-flex(row,flex-start,stretch);
                    flex-wrap: wrap;
                    padding: 0px 1% 0px 1%;
                    font-size: 1.4vmin;
                    color: var(--whapichat-blue);
                    position: relative;
                    animation-name: slide-top;
                    animation-duration: 0.2s;
                }

                @keyframes slide-top {
                    0% {
                        left: 10px;
                        top: 20px;
                    }

                    100% {
                        left: 0px;
                        top: 0px;
                    }
                }

                .input {
                    outline: none;
                    width: 100%;
                    border-bottom: 1px solid #000000;
                    padding: 1%;
                    font-size:1.6vmin;

                    &::placeholder {
                        color: #000000;
                    }

                    &:focus::placeholder {
                        color: #345282;
                    }

                    &:focus{
                        border-bottom: 1px solid #305691;
                    }
                }

                .input-counter {
                    width: 100%;
                    @include d-flex(row,flex-end,stretch);
                    flex-wrap: wrap;
                    color: #000000;
                    font-size: 1.1vmin;
                }
            }
        }

        .url-area {
            @include d-flex(column,flex-start,stretch);
            padding: 1%;
            flex: 1 0 1px;
            background-color: #ebebeb;
            border-radius: 10px;
            @include box-shadow;

            .copy-content{
                width:100%;
                @include d-flex(row,flex-end,center);
                margin-top:3px;

                .before-copy-text{
                    background-color:#000;
                    border-radius:10px;
                    color:white;
                    font-size:1vmin;
                }

                .after-copy-text{
                    background-color:green;
                    border-radius:10px;
                    color:white;
                    font-size:1vmin;
                }

                .copy-icon{
                    cursor:pointer; 
                    color:#000;
                }
            }

            .info-text{
                margin-top: 10px;
                padding: 0px 1% 0px 1%;
                word-break: break-word;
                font-size:1.5vmin;
            }

            p {
                padding: 0px 1% 0px 1%;
                font-size: 1.4vmin;
                color: #000000;
            }

            .input {
                font-size: 1.6vmin;
                outline: none;
                width: 100%;
                border-bottom: 1px solid #000000;
                padding: 1%;

                &::placeholder {
                    color: #B5B5B5;
                }
            }
        }
    }

    @media screen and (max-width:1024px) {
    .link-creator-card {
      @include d-flex(column,flex-start,stretch);
      gap: 50px;

      .input-areas{
          width: 100%;
      }

      .url-area{
          width: 100%;
      }
    }
  }
</style>